import { connect } from "react-redux";
import React, { Component } from 'react'
import ReactTooltip from "react-tooltip";
import { CARD_STYLE_INHERIT, IS_CARD_STYLE_CHANGE } from '../../../../../../../reducer/themes/reducersKeys';

 class InheritStyle extends Component {
  render() {
    const {inheritStyle} = this.props
    return (
        <div className="row align-items-center justify-content-between">
                <div className="col-9">
                  <label className="control-label mr-2 mb-0" htmlFor="webInheritStyle">
                  Use Your Website Settings (Recommended) 
                  <span className="svg-icon svg-icon-sm svg-icon-dark tooltips ms-1" data-tip="React-tooltip" data-for="toolTipInheritStyle" >
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                        <rect x="0" y="0" width="24" height="24"></rect>
                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle>
                        <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect>
                        <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect>
                      </g>
                    </svg>
                  </span>
                  </label>
                  <ReactTooltip multiline={true} id="toolTipInheritStyle" place="top" type="dark" effect="solid">
                  Properties of your websites will be automatically applied on the widget once you embed.
                  </ReactTooltip>
                </div>
                <div className="col-3 toggleOnBut justify-content-end">
                  <span className="switch justify-content-center">
                    <label htmlFor="webInheritStyle">
                      <input type="checkbox"  id="webInheritStyle" className="onoffswitch-checkbox updateStatus" defaultValue={inheritStyle}  checked={inheritStyle==1 ? true : false} onChange={(e) => { this.props.CARD_STYLE_INHERIT(Number(e.target.checked)) }} />
                      <span></span>
                    </label>
                  </span> 
                </div>
              </div>

 
    )
  }
}

const stateToProps = (state) => {
    return {
        inheritStyle:state.appData?.wall?.ThemeRule?.inheritStyles
    }
  }
  const DispatchToProps = dispatch => {
    return {
         CARD_STYLE_INHERIT: (data)=> {dispatch({type:CARD_STYLE_INHERIT, payload:data}); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true });},
    }
  }

export default connect(stateToProps, DispatchToProps)(InheritStyle)