import React, { Component } from 'react'
import { connect } from "react-redux";
import CatalogName from './elements/CatalogName';
import Products from './elements/Products';
import ProductButton from './elements/ProductButton';
import Hotspot from './elements/Hotspot';
import { UPDATE_CTA_ACTION } from '../../../../actions/moderationActions';
import { TOAST_SUCCESS } from '../../../../utils';
import { FEATURE_TEXT, FEATURE_TEXT_COLOR, FEATURE_CSS_FONT, FEATURE_FONT_VARIENT, FEATURE_FONT_SIZE, BUY_NOW_TEXT, BUTTON_CSS_FONT, BUTTON_FONT_VARIENT, BUY_NOW_COLOR, BUY_NOW_BACKGROUND, BUY_FONT_SIZE, PRODUCT_TITLE_COLOR, PRODUCT_TITLE_FONT_SIZE, TITLE_CSS_FONT, TITLE_FONT_VARIENT, ORIGINAL_PRICE_COLOR, PRICE_CSS_FONT, PRICE_FONT_VARIENT, ORIGINAL_PRICE_FONT_SIZE, DISCOUNT_PRICE_COLOR, DISCOUNT_CSS_FONT, DISCOUNT_FONT_VARIENT, DISCOUNT_PRICE_FONT_SIZE, HOTSPOT, HOTSPOT_COLOR, HOTSPOT_TYPE, HOTSPOT_TYPE_COLOR, STATUS, TAB_TARGET } from '../../../../reducer/themes/reducersKeys';
class CTAButton extends Component {
    state = {
        loadingCls:false,
        productId: this.props.ctaData.id,
        feature_text: this.props.ctaData.feature_text,
        feature_text_color: this.props.ctaData.feature_text_color,
        feature_css_font: this.props.ctaData.feature_css_font,
        feature_font_varient: this.props.ctaData.feature_font_varient,
        feature_font_size: this.props.ctaData.feature_font_size,

        buy_now_text: this.props.ctaData.buy_now_text,
        button_css_font: this.props.ctaData.button_css_font,
        button_font_varient: this.props.ctaData.button_font_varient,
        buy_now_color: this.props.ctaData.buy_now_color,
        buy_now_background: this.props.ctaData.buy_now_background,
        buy_font_size: this.props.ctaData.buy_font_size,

        product_title_color: this.props.ctaData.product_title_color,
        product_title_font_size: this.props.ctaData.product_title_font_size,
        title_css_font: this.props.ctaData.title_css_font,
        title_font_varient: this.props.ctaData.title_font_varient,

        original_price_color: this.props.ctaData.original_price_color,
        price_css_font: this.props.ctaData.price_css_font,
        price_font_varient: this.props.ctaData.price_font_varient,
        original_price_font_size: this.props.ctaData.original_price_font_size,

        discount_price_color: this.props.ctaData.discount_price_color,
        discount_css_font: this.props.ctaData.discount_css_font,
        discount_font_varient: this.props.ctaData.discount_font_varient,
        discount_price_font_size: this.props.ctaData.discount_price_font_size,

        hotspot: this.props.ctaData.hotspot,
        hotspot_color: this.props.ctaData.hotspot_color,
        hotspot_type: this.props.ctaData.hotspot_type,
        hotspot_type_color: this.props.ctaData.hotspot_type_color,
        status: this.props.ctaData.status,
        tab_target: this.props.ctaData.tab_target

    }

    onChangeFeature_text = (e) => { this.setState({ feature_text: e.target.value }) ; this.props.FEATURE_TEXT(e.target.value)};
    onChangeCatelogFont = (e) => { this.setState({ feature_css_font: e.label }) ; ; this.props.FEATURE_CSS_FONT(e.label)};
    onChangeCatelogVeriant = (e) => { this.setState({ feature_font_varient: e.label }); ; this.props.FEATURE_FONT_VARIENT(e.label) } 
    onChangeFeature_font_size = (e) => { this.setState({ feature_font_size: parseInt(e) }) ;  this.props.FEATURE_FONT_SIZE(parseInt(e))}
    onChangeStatus = () => { this.setState({ status: this.state.status==1 ? 0 : 1  }) ; this.props.STATUS(this.state.status==1 ? 0 : 1 )}

    onChangeButton_text = (e) => { this.setState({ buy_now_text: e.target.value }) ; this.props.BUY_NOW_TEXT(e.target.value)}; 
    onChangeButton_font = (e) => { this.setState({ button_css_font: e.label }) ; this.props.BUTTON_CSS_FONT(e.label)};
    onChangeButton_verient = (e) => { this.setState({ button_font_varient: e.label }) ; this.props.BUTTON_FONT_VARIENT(e.label)}
    onChangeButton_fontSize = (e) => { this.setState({ buy_font_size: parseInt(e) }) ; this.props.BUY_FONT_SIZE(parseInt(e))}

    // onChangeProduct_color = (event) => { this.setState({ product_title_color: event.target.value }); }
    onChangeProduct_font = (e) => { this.setState({ title_css_font: e.label }) ; this.props.TITLE_CSS_FONT(e.label)};
    onChangeProduct_verient = (e) => { this.setState({ title_font_varient: e.label }) ; this.props.TITLE_FONT_VARIENT(e.label)}
    onChangeProduct_fontSize = (e) => { this.setState({ product_title_font_size: parseInt(e) }); this.props.PRODUCT_TITLE_FONT_SIZE(parseInt(e)) }

    // onChangePrice_color = (event) => { this.setState({ original_price_color: event.target.value }); }
    onChangePrice_font = (e) => { this.setState({ price_css_font: e.label }) ; this.props.PRICE_CSS_FONT(e.label)};
    onChangePrice_verient = (e) => { this.setState({ price_font_varient: e.label }) ; this.props.PRICE_FONT_VARIENT(e.label)}
    onChangePrice_fontSize = (e) => { this.setState({ original_price_font_size: parseInt(e) }) ; this.props.ORIGINAL_PRICE_FONT_SIZE(parseInt(e))}

    // onChange_DisPrice_color = (event) => { this.setState({ discount_price_color: event.target.value }); }
    onChange_DisPrice_font = (e) => { this.setState({ discount_css_font: e.label }) ; this.props.DISCOUNT_CSS_FONT(e.label)};
    onChange_DisPrice_verient = (e) => { this.setState({ discount_font_varient: e.label }) ; this.props.DISCOUNT_FONT_VARIENT(e.label)}
    onChange_DisPrice_fontSize = (e) => { this.setState({ discount_price_font_size: parseInt(e) }) ; this.props.DISCOUNT_PRICE_FONT_SIZE(parseInt(e))}

    onChangeHotspotStatus = () => { this.setState({ hotspot: this.state.hotspot ==1 ? 0 : 1 }) ; this.props.HOTSPOT(this.state.hotspot ==1 ? 0 : 1)}
    onChangeHotspotType = () => { this.setState({ hotspot_type: this.state.hotspot_type ==1 ? 0 : 1 }) ; this.props.HOTSPOT_TYPE(this.state.hotspot_type ==1 ? 0 : 1)}

    onChange_tab_target = (e) => { this.setState({ tab_target: e.value });  this.props.TAB_TARGET(e.value ) }

    // onChange_Hotspot_color = (event) => { this.setState({ hotspot_color: event.target.value }); } 

    onSubmitCTA = () => { 
        this.setState({loadingCls:true})
        this.props.UPDATE_CTA_ACTION(this.state, this.onSuccess); 
        
    }
    onSuccess=()=>{
        // const {clickToCloseCta} = this.props;
        this.setState({loadingCls:false})
        // clickToCloseCta();
        // TOAST_SUCCESS('Successfully updated')
    }

    componentDidMount() { this.getMinicolrData()}
        getMinicolrData=()=>{
        const { feature_text_color, buy_now_color,buy_now_background, product_title_color, original_price_color, discount_price_color, hotspot_color , hotspot_type_color} = this.state
        var self = this;
        setTimeout(() => {
            window.$('#feature_text_color9').minicolors({
                control: 'hue',
                defaultValue: feature_text_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.ChangeFeature_text_color,
                theme: 'bootstrap'
            })
            window.$('#buy_now_color').minicolors({
                control: 'hue',
                defaultValue: buy_now_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChangeButton_color,
                theme: 'bootstrap'
            })
            window.$('#buy_now_background').minicolors({
                control: 'hue',
                defaultValue: buy_now_background,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChangeButton_background,
                theme: 'bootstrap'
            })
            window.$('#product_title_color').minicolors({
                control: 'hue',
                defaultValue: product_title_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChangeProduct_color,
                theme: 'bootstrap'
            })
            window.$('#original_price_color').minicolors({
                control: 'hue',
                defaultValue: original_price_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChangePrice_color,
                theme: 'bootstrap'
            })
            window.$('#discount_price_color').minicolors({
                control: 'hue',
                defaultValue: discount_price_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChange_DisPrice_color,
                theme: 'bootstrap'
            })
            window.$('#hotspot_color9').minicolors({
                control: 'hue',
                defaultValue: hotspot_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChange_Hotspot_color,
                theme: 'bootstrap'
            })
            window.$('#hotspot_type_color').minicolors({
                control: 'hue',
                defaultValue: hotspot_type_color,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChange_Hotspot_type_color,
                theme: 'bootstrap'
            })
        }, 1000)
    }

    componentDidUpdate(prevProps) { 
        if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
          if(this.props.is_cta_setting_change){ this.onSubmitCTA(); }
        }
      }


    ChangeFeature_text_color = (hex, opacity) => { if (!hex) return; this.setState({ feature_text_color: hex }); ; this.props.FEATURE_TEXT_COLOR(hex) }
    onChangeButton_color = (hex, opacity) => { if (!hex) return; this.setState({ buy_now_color: hex }); ; this.props.BUY_NOW_COLOR(hex) }
    onChangeButton_background = (hex, opacity) => { if (!hex) return; this.setState({ buy_now_background: hex }); ; this.props.BUY_NOW_BACKGROUND(hex) }
    onChangeProduct_color = (hex, opacity) => { if (!hex) return; this.setState({ product_title_color: hex }); ; this.props.PRODUCT_TITLE_COLOR(hex)}
    onChangePrice_color = (hex, opacity) => { if (!hex) return; this.setState({ original_price_color: hex }); ; this.props.ORIGINAL_PRICE_COLOR(hex) }
    onChange_DisPrice_color = (hex, opacity) => { if (!hex) return; this.setState({ feature_text_color: hex }) ; this.props.FEATURE_TEXT_COLOR(hex)}
    onChange_Hotspot_color = (hex, opacity) => { if (!hex) return; this.setState({ hotspot_color: hex }); this.props.HOTSPOT_COLOR(hex) }
    onChange_Hotspot_type_color = (hex, opacity) => { if (!hex) return; this.setState({ hotspot_type_color: hex }) ; this.props.HOTSPOT_TYPE_COLOR(hex)}

    render() {
        const { allFont, inheritStyle } = this.props
        this.getMinicolrData();
        return (
            <div className="accordion accordian_cta" id="ctaAccordian">
                <div className="accordion-item">
                    <h2 className="accordion-header" id="catalogHeading">
                        <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#catalog" aria-expanded="true" aria-controls="catalog">
                            Catalog Name
                        </button>
                    </h2>
                    <div id="catalog" className="accordion-collapse collapse show" aria-labelledby="catalogHeading" data-bs-parent="#ctaAccordian">
                        <div className="accordion-body">
                            <CatalogName inheritStyle={inheritStyle} onChangeCatelogFont={this.onChangeCatelogFont} allFont={allFont} {...this.state} onChangeCatelogVeriant={this.onChangeCatelogVeriant} onChangeFeature_font_size={this.onChangeFeature_font_size} onChangeStatus={this.onChangeStatus} onChangeFeature_text={this.onChangeFeature_text} />
                        </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="productHeading">
                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#product" aria-expanded="false" aria-controls="product">
                            Products
                        </button>
                    </h2>
                    <div id="product" className="accordion-collapse collapse" aria-labelledby="productHeading" data-bs-parent="#ctaAccordian">
                        <div className="accordion-body">
                            <Products  inheritStyle={inheritStyle}  allFont={allFont} onChangeProduct_font={this.onChangeProduct_font} onChangeProduct_verient={this.onChangeProduct_verient} onChangeProduct_fontSize={this.onChangeProduct_fontSize} onChangePrice_font={this.onChangePrice_font} onChangePrice_verient={this.onChangePrice_verient} onChangePrice_fontSize={this.onChangePrice_fontSize} onChange_DisPrice_font={this.onChange_DisPrice_font} onChange_DisPrice_verient={this.onChange_DisPrice_verient} onChange_DisPrice_fontSize={this.onChange_DisPrice_fontSize} {...this.state} />
                        </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="buttonHeading">
                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#buttonCTA" aria-expanded="false" aria-controls="Button">
                            Button
                        </button>
                    </h2>
                    <div id="buttonCTA" className="accordion-collapse collapse" aria-labelledby="buttonHeading" data-bs-parent="#ctaAccordian">
                        <div className="accordion-body">
                            <ProductButton  inheritStyle={inheritStyle} allFont={allFont} onChangeButton_text={this.onChangeButton_text} onChangeButton_font={this.onChangeButton_font} onChangeButton_verient={this.onChangeButton_verient} onChangeButton_fontSize={this.onChangeButton_fontSize} {...this.state} onChange_tab_target={this.onChange_tab_target} />
                        </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="hostpotHeading">
                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#hostpot" aria-expanded="false" aria-controls="Hostpot">
                            Hotspot
                        </button>
                    </h2>
                    <div id="hostpot" className="accordion-collapse collapse" aria-labelledby="hostpotHeading" data-bs-parent="#ctaAccordian">
                        <div className="accordion-body">
                            <Hotspot inheritStyle={inheritStyle}  allFont={allFont} onChangeHotspotStatus={this.onChangeHotspotStatus} onChangeHotspotType={this.onChangeHotspotType}  {...this.state} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        moderation: state.moderation,
        is_cta_setting_change:state.appData.is_cta_setting_change,
        save_and_publish:state.appData.save_and_publish,
        inheritStyle:state.appData?.wall?.ThemeRule?.inheritStyles
    }
}
const mapDispatchToProps = dispatch => {
    return {
        UPDATE_CTA_ACTION: (data, callback) => dispatch(UPDATE_CTA_ACTION(data, callback)),
        FEATURE_TEXT:(data)=>dispatch({type:FEATURE_TEXT, payload:data}),
        FEATURE_TEXT_COLOR:(data)=>dispatch({type:FEATURE_TEXT_COLOR, payload:data}),
        FEATURE_CSS_FONT:(data)=>dispatch({type:FEATURE_CSS_FONT, payload:data}),
        FEATURE_FONT_VARIENT:(data)=>dispatch({type:FEATURE_FONT_VARIENT, payload:data}),
        FEATURE_FONT_SIZE:(data)=>dispatch({type:FEATURE_FONT_SIZE, payload:data}),
        BUY_NOW_TEXT:(data)=>dispatch({type:BUY_NOW_TEXT, payload:data}),
        BUTTON_CSS_FONT:(data)=>dispatch({type:BUTTON_CSS_FONT, payload:data}),
        BUTTON_FONT_VARIENT:(data)=>dispatch({type:BUTTON_FONT_VARIENT, payload:data}),
        BUY_NOW_COLOR:(data)=>dispatch({type:BUY_NOW_COLOR, payload:data}),
        BUY_NOW_BACKGROUND:(data)=>dispatch({type:BUY_NOW_BACKGROUND, payload:data}),
        BUY_FONT_SIZE:(data)=>dispatch({type:BUY_FONT_SIZE, payload:data}),
        PRODUCT_TITLE_COLOR:(data)=>dispatch({type:PRODUCT_TITLE_COLOR, payload:data}),
        PRODUCT_TITLE_FONT_SIZE:(data)=>dispatch({type:PRODUCT_TITLE_FONT_SIZE, payload:data}),
        TITLE_CSS_FONT:(data)=>dispatch({type:TITLE_CSS_FONT, payload:data}),
        TITLE_FONT_VARIENT:(data)=>dispatch({type:TITLE_FONT_VARIENT, payload:data}),
        ORIGINAL_PRICE_COLOR:(data)=>dispatch({type:ORIGINAL_PRICE_COLOR, payload:data}),
        PRICE_CSS_FONT:(data)=>dispatch({type:PRICE_CSS_FONT, payload:data}),
        PRICE_FONT_VARIENT:(data)=>dispatch({type:PRICE_FONT_VARIENT, payload:data}),
        ORIGINAL_PRICE_FONT_SIZE:(data)=>dispatch({type:ORIGINAL_PRICE_FONT_SIZE, payload:data}),
        DISCOUNT_PRICE_COLOR:(data)=>dispatch({type:DISCOUNT_PRICE_COLOR, payload:data}),
        DISCOUNT_CSS_FONT:(data)=>dispatch({type:DISCOUNT_CSS_FONT, payload:data}),
        DISCOUNT_FONT_VARIENT:(data)=>dispatch({type:DISCOUNT_FONT_VARIENT, payload:data}),
        DISCOUNT_PRICE_FONT_SIZE:(data)=>dispatch({type:DISCOUNT_PRICE_FONT_SIZE, payload:data}),
        HOTSPOT:(data)=>dispatch({type:HOTSPOT, payload:data}),
        HOTSPOT_COLOR:(data)=>dispatch({type:HOTSPOT_COLOR, payload:data}),
        HOTSPOT_TYPE:(data)=>dispatch({type:HOTSPOT_TYPE, payload:data}),
        HOTSPOT_TYPE_COLOR:(data)=>dispatch({type:HOTSPOT_TYPE_COLOR, payload:data}),
        STATUS:(data)=>dispatch({type:STATUS, payload:data}),
        TAB_TARGET:(data)=>dispatch({type:TAB_TARGET, payload:data})
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CTAButton);