import React, { Component } from 'react'
import Select from 'react-select';
import InheritStyle from '../../../content/appsMore/modals/banner/elements/inheritStyle';
import ErrorFound from '../../../../ErrorFound';
import Slider from 'rc-slider';
class Products extends Component {
    styles = {
        option: (provided, state) => ({
            ...provided,
            fontFamily: state.data.font
        }),
        singleValue: (provided, state) => ({
            ...provided,
            fontFamily: state.data.font
        })
    };

    render() {
        const { product_title_color, title_css_font, title_font_varient, product_title_font_size, original_price_color, price_css_font, price_font_varient, original_price_font_size, discount_price_color, discount_css_font, discount_font_varient, discount_price_font_size,
            onChangeProduct_font, onChangeProduct_verient, onChangeProduct_fontSize, onChangePrice_font, onChangePrice_verient, onChangePrice_fontSize, onChange_DisPrice_font, onChange_DisPrice_verient, onChange_DisPrice_fontSize, allFont , inheritStyle} = this.props;

        var titleFontSelected = allFont.fonts.filter((itm) => itm.label == title_css_font)
        const tf = allFont.allFonts.filter((font) => font.family === title_css_font)[0]
        var veriantDataTitle = tf && tf.variants ? JSON.parse(tf.variants).map((f, i) => { return ({ 'label': f, 'value': i, 'className': 'redmmt', 'font': title_css_font }) }) : ""
        var titleVariantId = veriantDataTitle && veriantDataTitle.filter((itm) => itm.label == title_font_varient)
        var titleVariantId2 = veriantDataTitle && veriantDataTitle.filter((itm) => itm.value == 0)
        var titleVariantSelected = titleVariantId.length > 0 ? titleVariantId : titleVariantId2

        var priceFontSelected = allFont.fonts.filter((itm) => itm.label == price_css_font)
        const pf = allFont.allFonts.filter((font) => font.family === price_css_font)[0]
        var veriantDataPrice = pf && pf.variants ? JSON.parse(pf.variants).map((f, i) => { return ({ 'label': f, 'value': i, 'className': 'redmmt', 'font': price_css_font }) }) : ''
        var priceVariantId = veriantDataPrice && veriantDataPrice.filter((itm) => itm.label == price_font_varient)
        var priceVariantId2 = veriantDataPrice && veriantDataPrice.filter((itm) => itm.value == 0)
        var priceVariantSelected = priceVariantId.length > 0 ? priceVariantId : priceVariantId2

        var disPriFontSelected = allFont.fonts.filter((itm) => itm.label == discount_css_font)
        const df = allFont.allFonts.filter((font) => font.family === discount_css_font)[0]
        var veriantDataDisPri = df && df.variants ? JSON.parse(df.variants).map((f, i) => { return ({ 'label': f, 'value': i, 'className': 'redmmt', 'font': discount_css_font }) }) : ""
        var disPriVariantId = veriantDataDisPri && veriantDataDisPri.filter((itm) => itm.label == discount_font_varient)
        var disPriVariantId2 = veriantDataDisPri && veriantDataDisPri.filter((itm) => itm.value == 0)
        var disPriVariantSelected = disPriVariantId.length > 0 ? disPriVariantId : disPriVariantId2

        return ( <>
        <div className='buttonSec'>
            <div className='row'>
            <ErrorFound><InheritStyle /></ErrorFound>
                <div className='cardProperties darkLabel px-0'>
                    <div className='col-sm-12 form-group mb-2'>
                        <h6 className="titleCardStyle mt-3 mb-4">Title</h6>
                    </div>
                    <div className='row m-0'>
                        <div className="col-md-12">
                            <div className="form-group mb-0">
                                <div className="row">
                                    <div className={`col-12 mb-4`}>
                                        <label>Font Family</label>
                                        <Select  isDisabled={inheritStyle} value={titleFontSelected} onChange={onChangeProduct_font} options={allFont.fonts} styles={this.styles} />
                                    </div>
                                    <div className={`col-12 mb-4`} >
                                        <label>Weight</label>
                                        {veriantDataTitle.length ? (<Select  isDisabled={inheritStyle} value={titleVariantSelected} options={veriantDataTitle} onChange={onChangeProduct_verient} styles={this.styles} />) : null}
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-md-12 mt-3">
                        <div className={`form-group ${inheritStyle ? "ActiveInherit1":""}`} >
                                <label>Color</label>
                                <input type="text" className="form-control miniColors init" id="product_title_color" defaultValue={product_title_color} />
                                <label className="personalization-error-label"></label>
                            </div>
                        </div>

                        <div className="col-md-12  mt-3">
                            <div className="form-group">
                                <div className="row align-items-center">
                                    <div className={`col-12 mb-4`} >
                                        <label className="d-flex justify-content-between" htmlFor="productTilleSize">Font Size 
                                            <span>
                                                <span className="dataproductTilleSize" id="value-span">{product_title_font_size}</span>px
                                            </span>
                                        </label>
                                        <Slider  disabled={inheritStyle}  onChange={onChangeProduct_fontSize} trackStyle={{ backgroundColor: "#5354FC" }} value={product_title_font_size} min={10} max={25} />
                                        {/* <div className={`touchspin ${inheritStyle ? "ActiveInherit":""}`} >
                                            <button className="decrease" onClick={()=>onChangeProduct_fontSize(product_title_font_size-1)}>-</button>
                                              <input id="touchspin" type="text" name="touchspin" className="form-control" value={`${product_title_font_size}px`} readOnly />
                                            <button className="increase"  onClick={()=>onChangeProduct_fontSize(product_title_font_size+1)}>+</button>
                                        </div> */}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> 

                    <div className="col-sm-12 form-group mb-2 mt-4">
                        <h6 className="titleCardStyle mt-5 pt-6 mb-4 border-top">Price</h6>
                    </div>
                    <div className='row m-0'>
                        <div className="col-lg-12">
                            <div className="form-group mb-0">
                                <div className="row">
                                    <div className="col-12 mb-4">
                                        <label>Font Family</label>
                                        <Select  isDisabled={inheritStyle} value={priceFontSelected} onChange={onChangePrice_font} options={allFont.fonts} styles={this.styles} />
                                    </div>
                                    <div className="col-12 mb-4">
                                        <label>Weight</label>
                                        {veriantDataPrice.length ? (<Select  isDisabled={inheritStyle} value={priceVariantSelected} options={veriantDataPrice} onChange={onChangePrice_verient} styles={this.styles} />) : null}
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-12 mt-3">
                        <div className={`form-group ${inheritStyle ? "ActiveInherit1":""}`} >
                                <label>Color</label>
                                <input type="text" className="form-control miniColors init" id="original_price_color" defaultValue={original_price_color} />
                            </div>
                        </div>

                        <div className="col-lg-12 mt-3">
                            <div className="form-group">
                                <div className="row align-items-center">
                                    <div className={`col-12 mb-4`}>
                                        <label className="d-flex justify-content-between" htmlFor="productPriceSize">Font Size 
                                            <span>
                                                <span className="dataproductPriceSize" id="value-span">{original_price_font_size}</span>px
                                            </span>
                                        </label>
                                        {/* <div className={`touchspin ${inheritStyle ? "ActiveInherit":""}`}>
                                        <button className="decrease" onClick={()=>onChangePrice_fontSize(original_price_font_size-1)}>-</button>
                                              <input id="touchspin" type="text" name="touchspin" className="form-control" value={`${original_price_font_size}px`} readOnly />
                                            <button className="increase"  onClick={()=>onChangePrice_fontSize(original_price_font_size+1)}>+</button>

                                        </div> */}
                                        <Slider  disabled={inheritStyle} onChange={onChangePrice_fontSize} trackStyle={{ backgroundColor: "#5354FC" }} value={original_price_font_size} min={10} max={25} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
        

        
        </>)
    }
}

export default Products;