import React, { Component } from 'react'
import Select from 'react-select';
import InheritStyle from '../../../content/appsMore/modals/banner/elements/inheritStyle';
import ErrorFound from '../../../../ErrorFound';
import Slider from 'rc-slider';
class CatalogName extends Component {
    styles = {
        option: (provided, state) => ({ ...provided, fontFamily: state.data.font }),
        singleValue: (provided, state) => ({ ...provided, fontFamily: state.data.font })
    };

    class = {
        option: (provided, state) => ({ ...provided, class: state.data.class }),
        singleValue: (provided, state) => ({ ...provided, class: state.data.class })
    };

    render() {
        const { feature_text, feature_text_color, feature_css_font, feature_font_size, status, feature_font_varient, allFont, onChangeCatelogFont, onChangeCatelogVeriant, onChangeFeature_font_size, onChangeStatus , onChangeFeature_text, inheritStyle} = this.props

        var fontSelected = allFont.fonts.filter((itm) => itm.label == feature_css_font)
        const f = allFont.allFonts.filter((font) => font.family === feature_css_font)[0]
        var veriantData = f && f.variants ? JSON.parse(f.variants).map((f, i) => { return ({ 'label': f, 'value': i, 'className': 'redmmt', 'font': feature_css_font }) }) : []
        var variantId = veriantData && veriantData.filter((itm) => itm.label == feature_font_varient)
        var variantId2 = veriantData && veriantData.filter((itm) => itm.value == 0)
        var variantSelected =variantId.length > 0 ? variantId : variantId2
        return (<div className="row">
            <ErrorFound><InheritStyle /></ErrorFound>
            <div className="col-sm-12 mb-5">
                <div className='form-group mb-0'>
                    <label>Text</label>
                </div>
                <input type="text" className="form-control" id="featureText" defaultValue={feature_text} onChange={onChangeFeature_text} />
            </div>
            
            <div className="col-md-12">
                <div className="row">
                    <div className="col-12 mb-5">
                        <div className='form-group mb-0'>
                            <label>Font Family</label>
                        </div>
                        <Select  isDisabled={inheritStyle} value={fontSelected} onChange={onChangeCatelogFont} options={allFont.fonts} styles={this.styles}/>
                    </div>
                    <div className="col-12 mt-1 mb-5">
                        <div className='form-group mb-0'>
                            <label className="d-flex justify-content-between" htmlFor="featureFontSize">Weight</label>
                        </div>
                        {veriantData.length ? (<Select  isDisabled={inheritStyle} value={variantSelected} options={veriantData} onChange={onChangeCatelogVeriant} styles={this.styles}   />) : null}
                    </div>
                </div>
            </div>
            <div className="col-md-12">
                <div className="form-group">
                    <label>Font Color</label>
                    <div className={`minicolors minicolors-theme-bootstrap minicolors-position-bottom minicolors-position-left ${inheritStyle ? "ActiveInherit1":""}`} >
                    <input type="text" className="form-control miniColors init" id="feature_text_color9" aria-required="true" defaultValue={feature_text_color}/>
                    </div>
                </div>
            </div>
            <div className="col-md-12">
                <div className="form-group">
                    <div className="row align-items-center">
                        <div className="col-12 mt-1">
                            <label className="d-flex justify-content-between" htmlFor="featureFontSize">Font Size
                                <span><span className="datafeatureFontSize" id="value-span">{feature_font_size}</span>px</span>
                            </label>
                            {/* <div className={`touchspin ${inheritStyle ? "ActiveInherit":""}`}>
                                <button className='decrease'  onClick={ ()=>onChangeFeature_font_size(feature_font_size-1)}>-</button>
                                    <input id="touchspin" type="text" name="touchspin" value={`${feature_font_size}px`} className="form-control" readOnly />
                                <button className='increase' onClick={ ()=>onChangeFeature_font_size(feature_font_size+1)}>+</button>
                            </div> */}
                            <Slider disabled={inheritStyle} onChange={onChangeFeature_font_size} trackStyle={{ backgroundColor: "#5354FC" }} value={feature_font_size} min={10} max={25} />
                        </div>
                    </div>
                </div>
            </div>
            {/* <div className="col-12">
                <div className="form-group">
                    <div className="d-flex align-items-center justify-content-between">
                        <span className="mr-3">Status</span>
                        <span className="switch switch-xs">
                            <label className="col-form-label mb-0">
                                <input type="checkbox" name="status" defaultValue={status} id="personalizationStatus" className="onoffswitch-checkbox" data-off-color="#989898" data-on-color="#009385" onChange={onChangeStatus} checked={status ? true: false}/>
                                <span></span>
                            </label>
                        </span>
                    </div>
                </div>
            </div> */}
        </div>)
    }
}

export default CatalogName;