import React, { Component } from 'react'
import Select from 'react-select';
import InheritStyle from '../../../content/appsMore/modals/banner/elements/inheritStyle';
import ErrorFound from '../../../../ErrorFound';
import Slider from 'rc-slider';

const options = [
    { value: 1, label: 'Open New Tab' },
    { value: 2, label: 'Continue Same Tab' },
]
class ProductButton extends Component {

    styles = {
        option: (provided, state) => ({ ...provided, fontFamily: state.data.font }),
        singleValue: (provided, state) => ({ ...provided, fontFamily: state.data.font })
    };

    render() {
        const {buy_now_text, buy_now_color, buy_now_background , button_css_font, button_font_varient, buy_font_size,  onChangeButton_text, onChangeButton_font, onChangeButton_verient, onChangeButton_fontSize, allFont, onChange_tab_target, tab_target, inheritStyle} = this.props

        var fontSelected = allFont.fonts.filter((itm) => itm.label == button_css_font)
        const f = allFont.allFonts.filter((font) => font.family === button_css_font)[0]
        var veriantData = f && f.variants? JSON.parse(f.variants).map((f, i) => { return ({ 'label': f, 'value': i, 'className': 'redmmt', 'font': button_css_font }) }) : ""
        var variantId = veriantData && veriantData.filter((itm) => itm.label == button_font_varient)
        var variantId2 = veriantData && veriantData.filter((itm) => itm.value == 0)
        var variantSelected =variantId.length > 0 ? variantId : variantId2

 

        return (
            <>
            <div className="row">
            <ErrorFound><InheritStyle /></ErrorFound>
                <div className="col-md-12">
                    <div className="form-group">
                    <label>Text</label>
                    <input className="form-control" name="buy_now_text" id="buyText" type="text" defaultValue={buy_now_text} onChange={onChangeButton_text} />
                    <label className="personalization-error-label"></label>
                    </div>
                </div>

                <div className="col-md-12">
                    <div className="form-group mb-0">
                        <div className="row">
                            <div className="col-12 mb-4">
                                <label>Font Family</label>
                                <Select  isDisabled={inheritStyle} value={fontSelected} onChange={onChangeButton_font} options={allFont.fonts} styles={this.styles}/>
                            </div>
                            <div className="col-12 mb-4">
                                <label>Weight</label>
                                {veriantData.length ? (<Select  isDisabled={inheritStyle} value={variantSelected} options={veriantData} onChange={onChangeButton_verient} styles={this.styles}   />) : null}
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-md-12"> 
                    <div className={`form-group ${inheritStyle ? "ActiveInherit1":""}`} >
                        <label>Font Color</label>
                        <input  type="text" className="form-control miniColors init" id="buy_now_color" defaultValue={buy_now_color}  />
                        <label className="personalization-error-label"></label>
                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group">
                        <label>Background Color</label>
                        <input  type="text" className="form-control miniColors init"  id="buy_now_background"  defaultValue={buy_now_background}/>
                        <label className="personalization-error-label"></label>
                    </div>
                </div>
                
                <div className="col-md-12">
                    <div className="form-group">
                    <div className="row align-items-center">
                        <div className="col-12 mt-4">
                        <label className="d-flex justify-content-between" htmlFor="productPriceSize">Font Size 
                                            <span>
                                                <span className="dataproductPriceSize" id="value-span">{buy_font_size}</span>px
                                            </span>
                                        </label>
                            <Slider  disabled={inheritStyle}  onChange={onChangeButton_fontSize} trackStyle={{ backgroundColor: "#5354FC" }} value={buy_font_size} min={10} max={25} />
                            {/* <div className={`touchspin ${inheritStyle ? "ActiveInherit":""}`}>
                                <button className="decrease" onClick={()=>onChangeButton_fontSize(buy_font_size-1)}>-</button>
                                <input id="touchspin" type="text" name="touchspin" className="form-control" value={`${buy_font_size}px`} readOnly />
                                <button className="increase"  onClick={()=>onChangeButton_fontSize(buy_font_size+1)}>+</button> 
                            </div> */}
                        </div>
                    </div>
                    </div>
                </div>
                <div className='col-md-12'>
                    <div className="form-group">
                        <div className="row align-items-center">
                            <div className="col-12 mt-4">
                                <label>On Click</label>
                                <Select options={options} onChange={onChange_tab_target} value={options[options.findIndex(i => parseInt(i.value) == parseInt(tab_target))]}/> 
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
        )
    }
}

export default ProductButton;