import Uppy from '@uppy/core';
import { DashboardModal, Dashboard } from "@uppy/react";
import Transloadit from '@uppy/transloadit'; 
import GoogleDrive from "@uppy/google-drive";
import Instagram from "@uppy/instagram";
import Url from "@uppy/url";
import Webcam from "@uppy/webcam";
import "@uppy/core/dist/style.css";
import "@uppy/dashboard/dist/style.css";
import '@uppy/core/dist/style.min.css';
import '@uppy/dashboard/dist/style.min.css';
import '@uppy/webcam/dist/style.min.css';
import '@uppy/drag-drop/dist/style.min.css';
import '@uppy/status-bar/dist/style.min.css';
import { TOAST_ERROR } from '.';

const uppyKey = '0701d3d7f8d94e92a65a9cba388b1d96';
const uppyBucket = 'cloud.tagshop.ai';
const uppyAwsKey = '47ABVORKG52UYE1KB28S';
const uppyAwsSecret = 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v';
const companionUrl = "https://app.taggbox.com";



class UppyUpload {
    constructor(type) { 
        this.uppy = new Uppy({
            meta: { type: 'avatar' },
            autoProceed: true,
            debug: true,
            allowMultipleUploads: true,
            restrictions: {
                maxFileSize: 104857600,
                maxNumberOfFiles: 1,
                minNumberOfFiles: 1,
                allowedFileTypes: type ? type :['image/*']
            },
            // onBeforeFileAdded: beforeFileAdded,
            // onBeforeUpload: beforeUpload
        })

        this.uppy.use(Transloadit, {
            params: {
                auth: {
                    key: uppyKey
                },
                steps: {
                    store: {
                        robot: '/wasabi/store',
                        bucket_region: "us-west-1",
                        result: true,
                        bucket: uppyBucket,
                        key: uppyAwsKey,
                        secret: uppyAwsSecret,
                        path: "tmp/${file.id}.${file.ext}",
                    }
                }
            },
            waitForEncoding: true
        })

 this.uppy.use(Instagram, { target: Uppy.Dashboard, companionUrl: companionUrl, });
 this.uppy.use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: companionUrl, });
 this.uppy.use(Url, { target: Uppy.Dashboard, companionUrl: companionUrl });
 this.uppy.use(Webcam, { target: Uppy.Dashboard, companionUrl: companionUrl, });

 this.uppy.on("restriction-failed", (file, error) => {


  let fileType = file.type.split('/');

  if (fileType[0] != 'image') {
    TOAST_ERROR("You can only upload Image File")
    return false
  }


  if (file.size > 5000000) {
    TOAST_ERROR("Maximum allowed size of 4.8 MB")
    return false
  }

})

    }
}

export default UppyUpload;